<template>
  <div class="point-records">
    <mt-cell v-for="(item, i) in list" :key="i" :title="item.name" :label="item.operTime" :value="item.money"></mt-cell>
  </div>
</template>
<script>
export default {
  name: 'PointRecords',
  data () {
    const user = localStorage.getItem('user')
    return {
      pageNum: 1,
      user: (user && JSON.parse(user)) || {},
      list: []
    }
  },
  methods: {
    getMoneysByCondition (condition) {
      this.$socket.getMoneys(this.user.id, this.pageNum, (res) => {
        console.log(res)
        this.list = res.moneyList
      })
    }
  },
  created () {
    this.getMoneysByCondition('')
  }
}
</script>
<style lang="less">
  .point-records {
    .mint-cell-title {
      flex-direction: column;
      align-items: flex-start;
    }
  }
</style>
