<template>
  <div class="person-info">
    <mt-cell title="头像" is-link>
      <upload-img :uploadData="uploadData"></upload-img>
    </mt-cell>
    <mt-cell title="昵称" is-link :to="`/editName/${user.nickName}`" :value="user.nickName"></mt-cell>
    <mt-cell title="更多" is-link></mt-cell>
  </div>
</template>
<script>
import UploadImg from '@/components/UploadImg'
export default {
  name: 'PeronInfo',
  components: { UploadImg },
  data () {
    const user = localStorage.getItem('user')
    return {
      user: (user && JSON.parse(user)) || {},
      uploadData: {
        imgUrl: '', // 图片地址
        uploadUrl: '/api/file/upload' // 上传图片的地址
      }
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      this.uploadData.imgUrl = this.user.avatar
    }
  }
}
</script>
<style lang="less">
  .person-info {}
</style>
